*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --bg-color: #f6f6f6;
  --header-color: #fff;
  --text-color: #111;
  --slider-color: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --second-text-color: rgba(17, 17, 17, .6);
  --blue-color: #4f2ee8;
  --yellow-color: #eac645;
  --bg-shoppinglist: #d0d0d0;
  --category: #4f2ee8;
  --sing-btn: rgba(17, 17, 17, .5);
}

:root .close-btn {
  background-image: url("x_button_black.8d93f5fe.svg");
}

:root .email-img {
  background-image: url("email.black.576aaaca.svg");
}

:root .password-img {
  background-image: url("password_black.23ff0e78.svg");
}

:root .red_border {
  border-color: red;
}

:root .dark-mode {
  --sing-btn: rgba(255, 255, 255, .6);
  --category: #eac645;
  --bg-color: #202024;
  --header-color: #111;
  --text-color: #fff;
  --second-text-color: rgba(255, 255, 255, .6);
  --slider-color: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
}

:root .dark-mode .close-btn {
  background-image: url("x-button_white.27fa2fe3.svg");
}

:root .dark-mode .email-img {
  background-image: url("email_white.ded8126b.svg");
}

:root .dark-mode .password-img {
  background-image: url("password_white.5f0ff455.svg");
}

