* {
  -webkit-tap-highlight-color: transparent;
}

::-webkit-scrollbar {
  width: 15px;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

body {
  min-width: 320px;
  max-width: 1440px;
  background-color: var(--bg-color);
  color: var(--text-color);
  margin: 0 auto;
}

.main {
  margin-top: 112px;
  padding: 40px 24px;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

@media screen and (max-width: 1199px) {
  .main {
    flex-direction: column;
    align-items: center;
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  section {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .main {
    display: flex;
  }

  section {
    width: 1200px;
  }
}

.reg-modal-backdrop {
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 990;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .3s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.reg-modal-backdrop.show {
  opacity: 1;
}

.reg-form-popup {
  float: left;
  box-sizing: border-box;
  text-align: center;
  background-color: var(--header-color);
  width: 350px;
  height: auto;
  z-index: 9999;
  border: 2px solid #000;
  border-radius: 18px;
  padding-top: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.reg-form-register {
  flex-direction: column;
  justify-content: space-around;
  gap: 8px;
  padding: 20px;
  display: flex;
}

.close-btn {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: transform .1s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.close-btn:hover {
  width: 23px;
  height: 23px;
  top: 22px;
  right: 22px;
}

.reg-input-forms {
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--text-color);
  border: 2px solid;
  border-color: var(--text-color);
  width: auto;
  height: 100%;
  background-color: var(--header-color);
  border-radius: 40px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 15px;
  padding: 16px 11px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  transition: transform, all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.btn-queue-modal {
  width: auto;
  height: 100%;
  background-color: var(--text-color);
  border: 2px solid;
  border-color: var(--text-color);
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--header-color);
  border-radius: 40px;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  padding: 16px 116px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: 1px -100px 86px -37px rgba(113, 73, 211, .06);
}

.btn-queue-modal:hover, .btn-queue-modal:focus {
  background-color: var(--yellow-color);
  color: var(--text-color);
}

.clicked {
  transition: transform .2s ease-out;
  transform: translateY(2px);
}

.register-links {
  color: var(--text-color);
  justify-content: center;
  gap: 20px;
  padding-top: 10px;
  padding-bottom: 20px;
  text-decoration: none;
  display: flex;
}

.reg-signin-btn {
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--sing-btn);
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.reg-signin-btn:hover, .reg-signin-btn:focus, .reg-signin-btn:active {
  color: var(--blue-color);
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.reg-signup-btn {
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--sing-btn);
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.reg-signup-btn:hover, .reg-signup-btn:focus {
  color: var(--blue-color);
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.reg-signup-btn:active {
  border-color: var(--yellow-color);
  color: var(--blue-color);
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

@media screen and (min-width: 768px) {
  .reg-form-popup {
    width: 500px;
    padding-top: 40px;
  }
}

.user-img, .email-img {
  background-position: 90% 1rem;
  background-repeat: no-repeat;
  background-size: 16px 16px;
}

.email-img:hover {
  background-position: 91% 1rem;
  background-size: 18px 18px;
}

.password-img {
  background-position: 90% 1rem;
  background-repeat: no-repeat;
  background-size: 16px 16px;
}

.password-img:hover {
  background-position: 91% 1rem;
  background-size: 18px 18px;
}

.reg-input-forms:hover {
  border-radius: 3px;
}

.reg-modal-backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 990;
  background-color: rgba(46, 47, 66, .4);
  transition: transform, all .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.reg-checkbox {
  opacity: 0;
  display: block;
  position: absolute;
}

.reg-checkbox-custom {
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 3px;
  align-items: center;
  justify-items: center;
  display: flex;
}

.reg-checkbox-custom:after {
  content: "";
  width: 8px;
  height: 8px;
  visibility: hidden;
  background-color: #555;
  border-radius: 2px;
  margin: 3px;
}

.reg-checkbox:checked + .reg-checkbox-custom:after {
  visibility: visible;
}

.reg-checkbox-label {
  color: var(--text-color);
  margin-left: 6px;
}

.reg-checkbox-label:hover, .reg-checkbox-label:focus {
  color: var(--blue-color);
  text-decoration: underline;
}

.reg-checkbox:not(:checked) + .reg-checkbox-custom {
  background-color: #fcfbfb;
  border-color: #460101;
}

.reg-i-gree {
  margin: 0 auto;
  display: flex;
}

.conteiner-shopping-list {
